@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
body{
  font-family: 'Open Sans Condensed', sans-serif;
}
h2{
  text-align: center;
  color: purple;
}
h2.section-heading{
  color: purple;
  text-align: center;
}
.header{
    width:100%;
    height: 60px;
    background:purple;
    color: #FFF;
    text-align: center;
    margin-top: -20px;
}

h2{
    color:#FFF;
    line-height: 60px;
}
.task-form{
    margin-top: 60px;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: center;
    position: relative;
}

.task-input{
    padding: 1rem;
    width: 100%;
    display: block;
    color: purple;
    outline: none;
    border-radius: 5px;
    font-size: 16px;
    border: solid 1px purple;
}

.submit-button{
    padding: 1.15rem 1rem;
    background: purple;
    border-radius: 5px;
    color: #FFF;
    border: none;
    cursor: pointer;
    position:absolute;
    right: 1.5rem;
    top: 1.5rem;
    border-top-left-radius: 0%;
    border-bottom-left-radius: 0%;
}
.tasks-container{
    padding: 1.5rem;
}
.task-field{
    width: 100%;
    box-shadow: 0px 0px 2px 1px rgba(0,0,0,.1);
    padding: 0 .5rem;
    margin-bottom: 20px;
    color: purple;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    transition: all 13s linear;
}
.task-title{
    font-size: 20px;
}
.remove-btn{
    display: flex;
    align-items: center;
}
.delete-btn{
    fill: purple;
    width: 20px;
    height: 20px;
    margin-right: 20px;
    cursor: pointer;
}
